import React, { Component } from 'react';
// import $ from 'jquery';

class Table extends Component {

  render(){

    return (
        <div>
      {/* <button className="btn btn-primary" data-toggle="modal" data-target="#column_modal" style={{"float":"right","margin-bottom":"10px"}}>Add Column</button> */}
        <div className="box-body">

                            <table className="table table-bordered table-hover table-striped" id="list">
                              <TableHeader selectAll={this.props.selectAll} deselectAll={this.props.deselectAll} newKeys={this.props.newKeys} />
                              <tbody id="main_list">
                                  {this.props.candidateList.map((value,index)=>
                                  { let statusList = value.added_by[0].status;
                                    let finalStatus = statusList[statusList.length - 1].status,
                                    date=statusList[statusList.length-1].Timestamp;
			                        if(finalStatus === ""||finalStatus == null){
                                        finalStatus = statusList[statusList.length - 2].status;
                                        date=statusList[statusList.length-2].Timestamp;
                                        
                                    }
                                    
                                return (
                                  <tr key={index}>
                                       <td><input type="checkbox" value={JSON.stringify(value)} 
                                       checked={value.selected?true:false} onClick={this.selectCandidate.bind(this,index)}/></td>
                                      <td><a data-toggle="modal" data-target="#course_modal" className="btn btn-success" onClick={this.props.setCurrentCandidate.bind(null,value)}><i className="fa fa-edit"></i> </a></td>
                                      <td>{finalStatus}</td>
                                      <td>{new Date(parseInt(date)).toLocaleDateString()}</td>
                                      <td>{value.candidateName}</td>
                                      <td>{value.candidateEmail}</td>
                                      <td>{value.candidateContact}</td>
                                      <td>{value.work_experience[0].organization}</td>
                                      <td>{value.work_experience[0].role}</td>
                                      <td>{value.experience}</td>
                                      <td>{value.current_ctc}</td>
                                      <td>{value["et/ec"]}</td>
                                      <td>{value.noticePeriod}</td>
                                      <td>{value.college}</td>
                                      <td>{value.degree}</td>
                                      <td>{value.location}</td>
                                        {this.props.newKeys.map((newValue,index)=> {
                                            newValue=newValue==undefined?null:newValue;
                                            return (
                                                <td key={index}>{value[newValue]}</td>
                                            )
                                        })}
                                        <td></td>
                                  </tr>
                                      );
                                  })
                                  }
                              </tbody>
                              
                              {/* <TableFooter newKeys={this.props.newKeys}/> */}
                            </table>
                          </div>
                          </div>
    );
  }

  selectCandidate(index){
      let list=this.props.candidateList.slice();
      list[index].selected=!list[index].selected;
      this.props.selectCandidate(list);
  }

}

export default Table;


const TableFooter= (props)=>{
    return (<tfoot id="tfoot">
        <tr> 
          <th></th>
          <th>Edit</th>  
          <th>Status</th>
          <th>Last Action</th>
          <th>Name</th>
          <th>Mail Id</th>
          <th>Mobile</th>
          <th>Company</th>
          <th>Designation</th>
          <th>Experience</th>
          <th>CTC</th>
          <th>ET/EC</th>
          <th>Notice Period</th>
          <th>Univesity</th>
          <th>Degree</th>
          <th>Location</th>
          {props.newKeys.map((value, index)=> <th key={index}>{value==undefined?null:value}</th>)}
          <th></th>
        </tr>
        </tfoot>);
}
class TableHeader extends Component{
    constructor(props){
        super(props);
        this.state={
            allSelected:false
        };
    }
    
    render(){
    return (<thead id="thead">
        <tr>
          <th className="check"><input type="checkbox" checked={this.state.allSelected?true:false} onClick={this.manageSelectAll.bind(this)}/></th>
          <th>Edit</th>  
          <th>Status</th>
          <th>Last Action</th>
          <th>Name</th>
          <th>Mail Id</th>
          <th>Mobile</th>
          <th>Company</th>
          <th>Designation</th>
          <th>Experience</th>
          <th>CTC</th>
          <th>ET/EC</th>
          <th>Notice Period</th>
          <th>Univesity</th>
          <th>Degree</th>
          <th>Location</th>
          {this.props.newKeys.map((value, index)=> <th key={index}>{value==undefined?null:value}</th>)}
          <th><button className="btn btn-primary" data-toggle="modal" data-target="#newCol_modal">Add Column</button></th>
          </tr>
        </thead>);
    }
    manageSelectAll(){
        if(this.state.allSelected)this.props.deselectAll();
        else this.props.selectAll();
        this.setState({allSelected:!this.state.allSelected});
    }
}


